@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'DM Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --cell-size: 35px;
}

.board-owner-label {
  font-weight: bold;
}

.board-display-container {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.board-grid {
  display: grid;
  grid-template-rows: repeat(10, 35px);
  grid-template-rows: repeat(10, var(--cell-size));
  grid-template-columns: repeat(10, 35px);
  grid-template-columns: repeat(10, var(--cell-size));
  border: solid 2px gray;
  border-radius: 8px;
  margin: 20px 30px;
  box-shadow: rgb(219, 219, 219) 0px 0px 0px 2px inset;
  transition: opacity 0.9s cubic-bezier(0.9,0.1,0.9,0.1);
}

.disabled-board {
  pointer-events: none;
}

.dimmed-board {
  opacity: 0.45;
}
:root {
  --user-cell-color: rgb(211, 232, 235);
  --user-cell-border-color: rgb(140, 170, 190);
}

.board-cell {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: solid 1px rgb(219, 219, 219);
  color: transparent;
}

.cell-hoverable:hover {
  border: solid 2px rgb(63, 63, 63);
}

.cell-user {
  background-color: rgb(211, 232, 235);
  background-color: var(--user-cell-color);
  border-color: rgb(140, 170, 190);
  border-color: var(--user-cell-border-color);
}

.cell-known-empty {
  transition: all 1s;
  color: rgb(207, 207, 207);
  background-color:  rgb(240, 240, 240);
}

.cell-missed {
  animation: miss 0.2s forwards linear;
}

.cell-missed::after, .cell-known-empty::after {
  content: "·";
}

.cell-hit {
  animation: hit 0.2s forwards linear;
  border-color: rgb(201, 163, 93);
}

.cell-sunk {
  animation: sink 0.2s forwards cubic-bezier(.78,.11,.81,.56);
  font-size: 30px;
  padding-bottom: 3px;
  
}
.cell-sunk::after {
  content: "✕";
}

.cell-uninteractable {
  pointer-events: none;
}

.cell-uninteractable:hover {
  border: solid 1px rgb(219, 219, 219);
}

.cell-removeable {
  cursor: pointer;
}

.cell-top-left {
  border-top-left-radius: 6px;
}

.cell-top-right {
  border-top-right-radius: 6px;
}

.cell-bottom-left {
  border-bottom-left-radius: 6px;
}

.cell-bottom-right {
  border-bottom-right-radius: 6px;
}

@keyframes hit {
  from {
    background-color: rgb(158, 112, 26);
  } 
  to {
    background-color: rgb(241, 193, 102);
  }
}

@keyframes sink {
  from {
    color: transparent;
    background-color: rgb(245, 96, 96);
  }
  to {
    color: rgb(126, 47, 47);
    background-color: rgb(179, 73, 73);
    border-color: rgb(126, 47, 47);
  }
}

@keyframes miss {
  from {
    color: transparent;
    background-color: rgb(102, 102, 102);
  }
  to {
    color: rgb(207, 207, 207);
    background-color:  rgb(240, 240, 240);
  }
}
.sunk-ships-indicator-label {
  width: 120px;
  margin-bottom: 8px;
  margin-right: 10px;
  padding-bottom: 8px;
  border-bottom: solid 1px lightgray;
  font-size: 13px;
  font-style: italic;
  text-align: right;
}

.sunk-indicators-container {
  position: relative;
  left: 110px;
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 40px;
}

.sunk-indicator {
  margin: 0 12px;
}

.indicator-cell {
  box-sizing: border-box;
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 1px;
}

.indicator-not-sunk {
  background-color: rgb(238, 238, 238);
  border: solid 1px rgb(218, 218, 218);
}

.indicator-sunk {
  background-color: rgb(179, 73, 73);
  border: solid 1px rgb(134, 48, 48);
}
#end-message-container {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 12px;
  padding: 20px;
  width: 300px;
  border: solid 2px rgb(207, 207, 207);
  background-color: rgb(248, 248, 248);
  color: rgb(105, 105, 105);
}

#end-message-container h3, p {
  margin: 0;
  text-align: center;
}

#end-message-container h3 {
  color: rgb(70, 70, 70);
}

#play-again-button {
  margin-top: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 8px 15px;
  border: none;
  color:rgb(66, 77, 66);
  background-color: rgb(205, 226, 205);
  border-radius: 4px;
  font-family: inherit;
  font-size: 17px;
}

#play-again-button:hover {
  background-color: rgb(179, 206, 179);
}

#play-again-button:focus {
  outline: none;
}
.footer {
  position: fixed;
  width: 100%;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center;
}

.footer > * {
  margin: 0px 5px;
}
#ship-placer-container {
  margin-left: 30px;
  display: inline-flex;
  flex-direction: column;
  box-sizing: border-box;
  align-self: center;
  width: 250px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#ship-placer-container h3 {
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 20px;
}

.draggable-ship-group {
  display: flex;
}

.direction-toggle-container {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.direction-toggle-container p {
  margin: 0;
  margin-left: 5px;
  color: rgb(78, 78, 78);
  font-size: 14px;
}

.placement-option-button {
  display: flex;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 30px;
  margin-bottom: 15px;
  font-family: inherit;
  border: none;
  border-radius: 4px;
  padding: 2px 10px;
  cursor: pointer;
}

.placement-option-button:hover {
  background-color: rgb(221, 221, 221);
}

.placement-option-button:focus {
  outline: none;
}

.placement-option-icon {
  margin-right: 10px;
  color: rgb(66, 66, 66);
}

#manual-placement-text {
  margin-top: 5px;
  margin-bottom: 10px;
  padding-top: 8px;
  border-top: solid 1px rgb(196, 196, 196);
  font-weight: bold;
  font-size: 15px;
}

.placement-direction-button {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  width: 30px;
  height: 30px;
  border: none;
  margin-right: 10px;
  color: rgb(155, 155, 155);
  cursor: pointer;
  opacity: 0.8;
  border-radius: 4px;
}

.placement-direction-button:focus {
  outline: none;
}

.active-direction-button {
  background-color: rgb(191, 235, 233);
  color: rgb(91, 121, 129);
  cursor: default;
  opacity: 1;
  border: solid 1px rgb(152, 186, 192)
}

#start-button {
  background-color: rgb(204, 230, 204);
}

#start-button:hover {
  background-color: rgb(189, 216, 189);
}
.draggable-ship {
  position: relative;
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
  margin-right: 10px;
  pointer-events: none;
}

.ship-placer-cell {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: gray;
  background-color: rgb(231, 231, 231);
  border: solid 1px rgb(168, 168, 168);
  width: var(--cell-size);
  height: var(--cell-size);
  pointer-events: none;
}

.draggable-cell {
  cursor: move;
  pointer-events: auto;
}

.dragging-valid {
  background-color: rgb(195, 224, 175);
  border: solid 1px  rgb(155, 182, 138);
  color: rgb(137, 161, 121);
}

.dragging-invalid {
  background-color: rgb(238, 217, 217);
  border: solid 1px rgb(197, 167, 167);
  color: rgb(165, 125, 125);
}
#app-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

#game-container {
  margin-top: 50px;
}

@media only screen and (max-width: 830px) {
  #game-container {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 400px) {
  #game-container {
    position: relative;
    bottom: 120px;
    transform: scale(0.7);
    margin-top: 10px;
  }
}

#game-status {
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  margin: 0 auto;
  padding: 5px 30px;
  border-bottom: solid 2px rgb(207, 207, 207);
  background-color: rgb(248, 248, 248);
  color: rgb(105, 105, 105);
}
